import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles(theme => ({
  root: {
    width: '15%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
}));

function SimpleExpansionPanel() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>medicine</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            cyclofarm
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography className={classes.heading}>fastfood</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
           pizza
          </Typography>
         
        </ExpansionPanelDetails>
      </ExpansionPanel>
       <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>milk</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            1 litre
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      
       <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>juice</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            mango juice
          </Typography>
        </ExpansionPanelDetails>
        <ExpansionPanelDetails>
          <Typography>
            banana juice
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>


       <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>sprouts</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            pulses
          </Typography>
        </ExpansionPanelDetails>
        <ExpansionPanelDetails>
          <Typography>
            dal
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>

        <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>vegetable</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            potato
          </Typography>
        </ExpansionPanelDetails>
        <ExpansionPanelDetails>
          <Typography>
            leafy vegetable
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
  <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>edible oil</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            health oil
          </Typography>
        </ExpansionPanelDetails>
        <ExpansionPanelDetails>
          <Typography>
            sunflower oil
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>

      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>gheee vanaspati</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            ghee
          </Typography>
        </ExpansionPanelDetails>
        <ExpansionPanelDetails>
          <Typography>
            vanaspati
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>

        <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>salt and sugar</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
            salt
          </Typography>
        </ExpansionPanelDetails>
        <ExpansionPanelDetails>
          <Typography>
            sugar
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>


      
    </div>
  );
}

export default SimpleExpansionPanel;