import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import { fade } from '@material-ui/core/styles/colorManipulator';
import { withStyles } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import CartIcon from '@material-ui/icons/ShoppingCart';
import { styled } from '@material-ui/styles';
import { Link } from "react-router-dom";
import Slide from '@material-ui/core/Slide';

const Navigation = styled(AppBar)({
  background: '#e53935',
  position:'fixed',
  top:'0',
  height:'60px'
});

const Tool= styled(Toolbar)({
  height:'100px',
  paddingRight:'0px',
  paddingLeft:'0px'
});

const styles = theme => ({
  root: {
    width: '100%',
    height:'60px'
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
   
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    width: '90%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing.unit * 3,
      width: 'auto',
    },
  },
  searchIcon: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    marginLeft:'10px'
      },
  inputRoot: {
    color: 'inherit',
    width: '100%',
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 10,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 200,
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {

    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
});

class NavBar extends React.Component {
  state = {
    anchorEl: null,
    mobileMoreAnchorEl: null,
  };

  handleProfileMenuOpen = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuClose = () => {
    this.setState({ anchorEl: null });
    this.handleMobileMenuClose();
  };

  handleMobileMenuOpen = event => {
    this.setState({ mobileMoreAnchorEl: event.currentTarget });
  };

  handleMobileMenuClose = () => {
    this.setState({ mobileMoreAnchorEl: null });
  };

  render() {
    const { classes, Auth } = this.props;
  

    

    return (
        
        
      <div className={`${classes.root} `}>
         
        <Navigation position="static">
        <Slide direction="down" in={true} mountOnEnter unmountOnExit>
          <Tool>
           <div className=' flex w-100'> <IconButton className={classes.menuButton} color="inherit" aria-label="Open drawer" onClick={this.props.handleSlideBar}>
            
            </IconButton> 
              <div className={`ml3 mt2 mr3 white ${classes.search}`}>
              <div className={` ${classes.searchIcon}`}>
              
              </div>
            
            </div>
            <Link to='/'>
 <div className={classes.grow} />
</Link>
            </div>
           
        
          </Tool>
          </Slide>
        </Navigation>
    
      </div>


    );
  }
}

NavBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(NavBar);