import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/ArrowBack';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import { styled } from '@material-ui/styles';
import { Link } from "react-router-dom";
import SearchIcon from '@material-ui/icons/Search';
import Slide from '@material-ui/core/Slide';

const Navigation = styled(AppBar)({
  background: '#e53935', 
  position:'fixed',
  top:'0',
  height:'60px',
});
const styles = {
  root: {
    flexGrow: 1
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
};

class MenuAppBar extends React.Component {
  state = {
    auth: true,
    anchorEl: null,
    visible:true,
    prevScrollpos: window.pageYOffset,
  };
 // Adds an event listener when the component is mount.
 componentDidMount() {
  window.addEventListener("scroll", this.handleScroll);
}

// Remove the event listener when the component is unmount.
componentWillUnmount() {
  window.removeEventListener("scroll", this.handleScroll);
}

// Hide or show the menu.
handleScroll = () => {

  const currentScrollPos = window.pageYOffset;
  const visible = this.state.prevScrollpos > currentScrollPos;
  this.setState({
    prevScrollpos: currentScrollPos,
    visible
  });
};

  handleChange = event => {
    this.setState({ auth: event.target.checked });
  };

  handleMenu = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  render() {
    const { classes } = this.props;
    const { auth, anchorEl , visible} = this.state;
    const open = Boolean(anchorEl);

    return (
      <div className={` ${classes.root}`}>
       {visible? <Navigation position="static">
       <Slide direction="down" in={true} mountOnEnter unmountOnExit>
          <Toolbar>
            <IconButton className={classes.menuButton} color="inherit" onClick={()=>this.props.history.goBack()} aria-label="Menu">
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" color="inherit" className={classes.grow}>
             {this.props.title}
            </Typography>
            {auth && (
              <div>
                <Link to ='search' >   <IconButton
                  aria-owns={open ? 'menu-appbar' : undefined}
                  aria-haspopup="true"
                  onClick={this.handleMenu}
                  color="inherit"
                >
                 <span className='white'>   <SearchIcon/></span>
                </IconButton></Link>
               <Link to='/myaccount'> <IconButton
                  color="inherit"
                ><span className='white'>
                  <AccountCircle /></span>
                </IconButton>
                </Link>
              </div>
            )}
          </Toolbar>
          </Slide>
        </Navigation>:null}
      </div>
    );
  }
}

MenuAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MenuAppBar);